*,
*::before,
*::after {
  box-sizing: border-box;
}

body {
  margin: 0;
}

img {
  max-width: 100%;
  height: auto;
  object-fit: cover;
  display: block;
}

@font-face {
  font-weight: 400;
  font-style: normal;
  font-family: "Open Sans";
  src: url("../fonts/opensans.woff2") format("woff2"), url("../fonts/opensans.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-weight: 700;
  font-style: normal;
  font-family: "Open Sans";
  src: url("../fonts/opensansbold.woff2") format("woff2"), url("../fonts/opensansbold.woff") format("woff");
  font-display: swap;
}
.page {
  height: 100%;
  font-family: "Open Sans", "Arial", sans-serif;
  font-style: normal;
  font-size: 16px;
  line-height: 20px;
  font-weight: 400;
  color: rgb(68, 68, 68);
  background-color: rgb(255, 255, 255);
}

.page__body {
  display: flex;
  flex-direction: column;
  min-height: 100%;
  margin: 0;
  background-color: rgb(255, 255, 255);
}
@media (min-width: 768px) {
  .page__body {
    min-width: 768px;
  }
}
@media (min-width: 1150px) {
  .page__body {
    min-width: 1150px;
  }
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  clip: rect(0 0 0 0);
  margin: -1px;
  padding: 0;
  width: 1px;
  height: 1px;
  border: 0;
  white-space: nowrap;
  clip-path: inset(100%);
}

.button-primary {
  display: block;
  padding: 0;
  outline: 0;
  border: none;
  background-color: transparent;
  cursor: pointer;
  max-width: 260px;
  margin: 0 auto;
  padding: 15px 0;
  font-family: "Open Sans", "Arial", sans-serif;
  font-weight: 700;
  font-size: 18px;
  line-height: 20px;
  text-decoration: none;
  text-transform: uppercase;
  text-align: center;
  border: 2px solid rgb(68, 68, 68);
  color: rgb(68, 68, 68);
}
.button-primary:hover, .button-primary:focus-within {
  color: rgb(255, 255, 255);
  background-color: rgb(68, 68, 68);
}
.button-primary:active {
  color: rgba(255, 255, 255, 0.3);
}
.button-primary--green {
  color: rgb(255, 255, 255);
  border-color: rgb(99, 209, 187);
  background-color: rgb(99, 209, 187);
}
.button-primary--green:hover {
  border: 2px solid rgb(68, 68, 68);
}

