.section-wavy::after {
  content: "";
  position: absolute;
  bottom: 0;
  width: 100%;
  height: 6px;
  background-image: url("../img/svg/zigzag-line-icon.svg");
  background-repeat: repeat-x;
  z-index: 10;
}
@media (min-width: 768px) {
  .section-wavy::after {
    height: 17px;
    background-image: url("../img/svg/zigzag-line-desktop-icon.svg");
  }
}
@media (min-width: 1150px) {
  .section-wavy::after {
    height: 23px;
    transform: translateX(-50%);
    left: 50%;
    max-width: 1050px;
  }
  .section-wavy--white {
