.main-navigation {
  width: 100%;
}
@media (max-width: 767.9px) {
  .main-navigation--closed .main-navigation__list {
    display: none;
  }
  .main-navigation--nojs .main-navigation__list:nth-child(-n+2) {
    display: block;
  }
}
@media (min-width: 1150px) {
  .main-navigation {
    max-width: 1150px;
  }
}

.main-navigation__toogle {
  position: absolute;
  top: 29px;
  right: 30px;
  display: block;
  width: 21px;
  height: 21px;
  padding: 0;
  outline: 0;
  border: none;
  background-color: transparent;
  cursor: pointer;
  z-index: 2;
}
.main-navigation__toogle:focus-within, .main-navigation__toogle:hover, .main-navigation__toogle:active {
  opacity: 0.5;
}
@media (min-width: 768px) {
  .main-navigation__toogle {
    display: none;
  }
}

.main-navigation--closed .main-navigation__toogle::before {
  content: "";
  position: absolute;
  top: 3px;
  right: 0;
  width: 21px;
  height: 2px;
  background-color: #000000;
  box-shadow: 0 6px 0 0 #000000, 0 12px 0 0 #000000;
}

.main-navigation--closed .main-navigation__toogle:active::before {
  background-color: rgba(0, 0, 0, 0.3);
  box-shadow: 0 6px 0 0 rgba(0, 0, 0, 0.3), 0 12px 0 0 rgba(0, 0, 0, 0.3);
}

.main-navigation--opened .main-navigation__toogle {
  position: absolute;
}
.main-navigation--opened .main-navigation__toogle::before, .main-navigation--opened .main-navigation__toogle::after {
  content: "";
  position: absolute;
  left: 0;
  width: 21px;
  height: 2px;
  background-color: #000000;
}
.main-navigation--opened .main-navigation__toogle::before {
  transform: rotate(45deg);
  box-shadow: none;
}
.main-navigation--opened .main-navigation__toogle::after {
  transform: rotate(-45deg);
}
.main-navigation--opened .main-navigation__toogle:active::before, .main-navigation--opened .main-navigation__toogle:active::after {
  background-color: rgba(0, 0, 0, 0.3);
}

.main-navigation--nojs .main-navigation__toogle {
  display: none;
}

@media (max-width: 767.9px) {
  .main-navigation--nojs .main-navigation__inner-popover {
    position: static;
    min-height: 0;
  }
}

.main-navigation__inner-popover {
  position: absolute;
  width: 100%;
  z-index: 10;
}
@media (min-width: 768px) {
  .main-navigation__inner-popover {
    top: 35px;
    position: relative;
    display: grid;
    grid-template-columns: 1fr 1fr 80px 268px;
    grid-template-areas: "logo logo navu navu" "navs navs . promo";
    background-color: transparent;
  }
}
@media (min-width: 1150px) {
