    grid-template-columns: 436px 1fr 80px 268px;
    grid-template-areas: "navs logo navu navu" "navp promo promo promo";
    margin: 0 auto;
    width: calc(100% - 100px);
    max-width: 1050px;
    width: auto;
  }
}

