@use "../abstracts/bootstrap.scss";

.form {
  display: flex;
  flex-direction: column;
}

.form__group {
  margin-bottom: 21px;

  &--email {
    margin: 0;
  }

  &--phone {
    margin-bottom: 33px;
  }

  &--other {
    margin-bottom: 7px;
  }

  @media (min-width: bootstrap.$tablet-width) {
    margin: 0;

    &--radio {
      margin-bottom: 5px;
    }

    &--checkbox {
      margin-bottom: 7px;
    }

    &:nth-child(even) {
      background-color: rgba(241, 241, 241, 1);
    }

    &--phone {
      margin-bottom: 35px;
    }

    &--phone,
    &--email {
      display: grid;
      grid-template-columns: 150px 1fr;
      column-gap: 15px;

      @media (min-width: bootstrap.$desktop-width) {
        grid-template-columns: 100px 1fr;
      }
    }
  }
}

.form__group-wrapper {
  display: grid;
  margin-bottom: 11px;

  @media (min-width: bootstrap.$tablet-width) {
    grid-template-columns: 150px 1fr;
    column-gap: 14px;
    padding-top: 50px;
    padding-bottom: 50px;

    @include bootstrap.wrapper-tablet;

    &--unification {
      display: block;
    }
  }

  @media (min-width: bootstrap.$desktop-width) {
    grid-template-columns: 100px 1fr;
    padding-left: 60px;
    padding-right: 178px;

    @include bootstrap.wrapper-desktop;
  }
}

.form__group-inner {
  margin: 0;

  &:not(:last-child) {
    margin-bottom: 24px;
  }

  @media (min-width: bootstrap.$tablet-width) {
    font-size: 19px;

    &--name {
      display: flex;
      align-items: center;
    }

    &--name > .form__item-label {
      padding-right: 25px;
    }
  }
}

.form__item-label {
  position: relative;
  font-weight: 700;
  font-size: 17px;
  line-height: 18px;
}

.form__item-input {
  width: 100%;
  padding: 17px 0;
  border: none;
  border-bottom: 2px solid bootstrap.$colorBlack;
  box-sizing: border-box;
  overflow: hidden;
  background-color: transparent;

  &:hover,
  &:focus-within {
    outline: none;
    border-color: bootstrap.$colorGreen-line;
  }

  &:hover ~ .form__item-icon,
  &:focus-within ~ .form__item-icon {
    color: rgba(bootstrap.$colorGreen-hover, 1);
  }

  &:active {
    outline: none;
    border-color: bootstrap.$colorGreen-line;
  }

  &::placeholder {
    font-family: bootstrap.$open-sans;
    font-size: 17px;
    line-height: 18px;
    opacity: 0.3;

    @media (min-width: bootstrap.$tablet-width) {
      font-size: 19px;
    }
  }

  &--mail {
    position: relative;
  }

  &--textarea {
    appearance: none;
    border: 2px solid bootstrap.$colorBlack;
    padding: 17px;
    padding-bottom: 89px;
    resize: none;
  }

  @media (min-width: bootstrap.$tablet-width) {
    padding-left: 17px;
  }
}

.form__item-icon {
  position: absolute;
  top: 0;
  right: 0;
  color: rgba(bootstrap.$colorBlack, 0.2);
  fill: currentColor;

  @media (min-width: bootstrap.$tablet-width) {
    right: 28px;
  }

  @media (min-width: bootstrap.$desktop-width) {
    right: 0;
  }
}

.form__legend {
  position: relative;
  display: inline-block;
  width: 100%;
  margin: 0;
  padding: 0;
  font-weight: 700;
  font-size: 17px;
  line-height: 16px;
  color: bootstrap.$colorGreen-primary;
  text-transform: uppercase;

  &--line::after {
    content: "";
    position: absolute;
    bottom: 0;
    left: 63px;
    right: 0;
    height: 50%;
    border-top: 1px solid bootstrap.$colorGreen-line;
  }

  &--radio {
    margin-bottom: 30px;
  }

  &--checkbox {
    margin-bottom: 24px;
  }

  &--name {
    margin-bottom: 29px;
  }

  &--other {
    margin-bottom: 23px;
  }

  @media (min-width: bootstrap.$tablet-width) {
    padding-top: 17px;

    &--line::after {
      display: none;
    }
  }
}

.form__list {
  display: grid;
  grid-template-columns: auto;

  &--radio {
    gap: 11px;
  }

  &--checkbox {
    gap: 23px;
  }

  @include bootstrap.ul-reset;

  @media (min-width: bootstrap.$tablet-width) {
    &--radio {
      gap: 35px;
    }

    &--checkbox {
      grid-template-columns: 1fr 1fr;
      column-gap: 15px;
      row-gap: 40px;
      padding-top: 3px;
    }
  }

  @media (min-width: bootstrap.$desktop-width) {
    &--checkbox {
      grid-template-columns: repeat(3, 1fr);
      column-gap: 95px;
    }
  }
}

.form__inner-button {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;

  @media (min-width: bootstrap.$tablet-width) {
    flex-direction: row;
    justify-content: space-between;
    gap: 20px;

    @include bootstrap.wrapper-tablet;
  }

  @media (min-width: bootstrap.$desktop-width) {
    width: 700px;
    justify-content: space-between;
    gap: 10px;
  }
}

.form__remark {
  margin: 0;
  margin-right: auto;
  color: bootstrap.$colorDarkGrey;
  font-size: 14px;
  line-height: 16px;

  @media (min-width: bootstrap.$tablet-width) {
    margin: 0;
    display: flex;
    align-items: center;
    justify-content: flex-end;
    order: 2;
    font-size: 17px;
    line-height: 30px;
  }
}

.form__button-wrapper {
  display: grid;
  grid-template-columns: 230px 1fr;
}

.form__button {
  box-sizing: border-box;
  width: 100%;
  max-width: 100%;
  margin-top: 30px;

  @media (min-width: bootstrap.$tablet-width) {
    max-width: 230px;
    margin: 0;
    order: 1;
  }
}
